/**
 * Genereaz o parol temporar sigur care conine:
 * - Cel puin o liter mare
 * - Cel puin o liter mic
 * - Cel puin un numr
 * - Cel puin un caracter special
 * - Lungime minim de 12 caractere
 */
export const generateTempPassword = (): string => {
  const lowercase = 'abcdefghijklmnopqrstuvwxyz';
  const uppercase = 'ABCDEFGHIJKLMNOPQRSTUVWXYZ';
  const numbers = '0123456789';
  const special = '@#$%^&*';
  
  // Asigurm c avem cel puin cte unul din fiecare
  const password = [
    lowercase[Math.floor(Math.random() * lowercase.length)],
    uppercase[Math.floor(Math.random() * uppercase.length)],
    numbers[Math.floor(Math.random() * numbers.length)],
    special[Math.floor(Math.random() * special.length)],
  ];
  
  // Completm pn la lungimea dorit
  const allChars = lowercase + uppercase + numbers + special;
  while (password.length < 12) {
    password.push(allChars[Math.floor(Math.random() * allChars.length)]);
  }
  
  // Amestecm caracterele
  return password.sort(() => Math.random() - 0.5).join('');
}; 